import 'tui-image-editor/dist/tui-image-editor.css'
import ImageEditor from '@toast-ui/react-image-editor'
import { Component, createRef } from 'react'

export default class App extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      picture: null,
      axelorAppName: '',
      domain: '',
      id: null,
      version: null,
      fileName: null,
      w: 640,
      h: 640
    }

    this.getPicture = this.getPicture.bind(this)
    this.getUsrImg = this.getUsrImg.bind(this)
    this.save = this.save.bind(this)

    this.editorRef = createRef()
  }

  getPicture() {
    const params = new URLSearchParams(document.location.search)

    this.setState({
      axelorAppName: params.get('projectName'),
      domain: params.get('domain'),
      id: params.get('id')
    }, () => {
      console.log(this.state)
      if(this.state.id){
        console.log(`/${this.state.axelorAppName}/ws/rest/${this.state.domain}/${this.state.id}/`)
        fetch(`/${this.state.axelorAppName}/ws/rest/${this.state.domain}/${this.state.id}/`, {
          method: 'GET',
          credentials: 'include',
          headers: { 'Accept': 'application/json' }
        })
        .then(response => {
          if (response.headers.get('content-type').includes('json')) return response.json()
        })
        .then(json => { 
          console.log(json.data[0])
          this.setState({
              picture: `/${this.state.axelorAppName}/ws/rest/com.axelor.meta.db.MetaFile/${json.data[0].picture.id}/content/download`,
              id: json.data[0].picture.id,
              version: json.data[0].picture.$version,
              fileName: json.data[0].picture.fileName
            })
        })
        .catch(err => console.error(err))
      }
    })
  }

  getViewportSize() {
    const width = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
    const height = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0)
    return {width: width, height: height}
  }

  componentDidMount() {
    this.getPicture()
    
    const domSize = this.getViewportSize()

    this.setState({
      w: domSize.width,
      h: domSize.height - 1
    })
  }

  save() {
    console.log("saving...")
    const editorInstace = this.editorRef.current.getInstance()
    const imgBase64 = editorInstace.toDataURL()
    const imgBlob = this.dataURItoBlob(imgBase64)
    
    if(this.state.id != null && this.state.version != null) {
      let formData = new FormData()
      
      formData.append('file', imgBlob)
      formData.append('field', undefined)
      formData.append('request', JSON.stringify({"data":{
        "fileName": this.state.fileName,
        "fileType": imgBlob.type,
        "fileSize": imgBlob.size,
        "id": this.state.id,
        "$upload": {"file": {}}
      }}))
      
      fetch(`/${this.state.axelorAppName}/ws/rest/com.axelor.meta.db.MetaFile/upload/`, {
        method: 'POST',
        credentials: 'include',
        headers: {
          // 'Accept': 'application/json',
          // 'Content-Type': 'application/octet-stream',
          // 'Connection': 'keep-alive',
          // 'Content-length': imgBase64.length.toString(),
          // 'Pragma': 'no-cache',
          // 'Cache-Control': 'no-cache',
          // 'X-File-Offsest': 0,
          // 'x-File-Type': 'image/png',
          // 'X-File-Name': this.state.fileName,
          // 'X-Requested-With': 'XMLHttpRequest',
          'X-CSRF-Token': this.readCookie('CSRF-TOKEN')
        },
        body: formData
      })
      .then(response => {
        if (response.headers.get('content-type').includes('json')) return response.json()
        const editorInstace = this.editorRef.current.getInstance()
        editorInstace.loadImageFromURL(this.state.picture, this.state.fileName)
      })
      .then(json => {
          console.log(json)
          this.getPicture()
      })
      .catch(err => console.error(err))
    }
  }

  getUsrImg() {   
    const editorInstace = this.editorRef.current.getInstance()
    editorInstace.loadImageFromFile(document.getElementById('img-upload').files[0])
    this.setState({ fileName: document.getElementById('img-upload').files[0].name })

    // let reader = new FileReader()
    // reader.onloadend = () => {console.log("Result: ", reader.result)}
    // reader.readAsDataURL(file)
  }

  readCookie(name) {
    let cookieString = document.cookie || ''
    let cookies = cookieString.split('; ').reduce((obj, value) => {
      let parts = value.split('=')
      obj[parts[0]] = parts[1]
      return obj
    }, {})
    return cookies[name]
  }

  dataURItoBlob(dataURI) {
    // convert base64 to raw binary data held in a string
    // doesn't handle URLEncoded DataURIs - see SO answer #6850276 for code that does this
    var byteString = atob(dataURI.split(',')[1]);

    // separate out the mime component
    var mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

    // write the bytes of the string to an ArrayBuffer
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);
    for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }

    //Old Code
    //write the ArrayBuffer to a blob, and you're done
    //var bb = new BlobBuilder();
    //bb.append(ab);
    //return bb.getBlob(mimeString);

    //New Code
    return new Blob([ab], {type: mimeString});
  }
  
  render() {   
    return (
      this.state.picture ?
      <div className="App">
        <input type='file' className='editor-img-upload' id='img-upload' onChange={this.getUsrImg} accept='image/*'/>
        <input type='button' className='editor-btn' value='Save' onClick={this.save}/>
        <input type='button' className='editor-btn' value='Load' onClick={() => {document.getElementById('img-upload').click()}}/>
        <ImageEditor ref={this.editorRef}
          includeUI={{
            loadImage: {
              path: this.state.picture,
              name: this.state.fileName,
            },
            menu: ['resize', 'crop', 'flip', 'rotate', 'draw', 'shape', 'icon', 'text', 'mask', 'filter'],
            uiSize: {
              width: 'auto',
              height: `${this.state.h}px`,
            },
            menuBarPosition: 'bottom',
          }}
          cssMaxHeight={Math.round(this.state.h/1.3)}
          cssMaxWidth={Math.round(this.state.w/1.3)}
          selectionStyle={{
            cornerSize: 20,
            rotatingPointOffset: 70,
          }}
          usageStatistics={false}
        />
      </div>
      :
      <div className="App">
        <h1>Could not load picture</h1>
      </div>
    )
  }
}
